import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";

import store from "./app/store";
import { Provider } from "react-redux";
import MultipleTabsOpened from "./components/MultipleTabsOpened";

const DATA = [];
let tabsOpened = JSON.parse(localStorage.getItem("tabsOpened"));

if (!tabsOpened || tabsOpened <= 0) localStorage.setItem("tabsOpened", "1");
else {
  console.log("updated");
  tabsOpened++;
  localStorage.setItem("tabsOpened", JSON.stringify(tabsOpened));
}

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <Provider store={store}>
      {tabsOpened >= 2 ? (
        <MultipleTabsOpened tabsOpened={tabsOpened} />
      ) : (
        <App data={DATA} />
      )}
    </Provider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
